@import url('https://fonts.googleapis.com/css2?family=Arima&family=Montserrat:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap');

:root {
  --acDarkBlue: #006185;
  --acLightBlue: #609CBB;
  --acYellow: #E9BF00;
  --acWhite: #EDE3E4;
  --acPurple: #E7DFE8;
}

* {
  color: white;
}

#root {
  width: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  background-color: var(--acDarkBlue);
}

.App {
  /* overflow-x: hidden; */
  text-align: center;
  width: 100%;
}

.Layout {
  width: 100%;
}

